// showMsg("dev 初始化")
chrome.devtools.panels.create("GameInspect", "icon.png", "gameInspect.html", function (panel) {
    panel.onShown.addListener(function (win) {
        chrome.runtime.sendMessage({
            type: "devPanelStateChange" /* MessageType.DevPanelStateChange */,
            data: { isShow: true, tabId: chrome.devtools.inspectedWindow.tabId },
            from: "DevPanel" /* MessageFromType.DevPanel */
        });
    });
    panel.onHidden.addListener(function () {
        chrome.runtime.sendMessage({
            type: "devPanelStateChange" /* MessageType.DevPanelStateChange */,
            data: { isShow: false, tabId: chrome.devtools.inspectedWindow.tabId },
            from: "DevPanel" /* MessageFromType.DevPanel */
        });
    });
});
